@font-face {
  font-family: 'GTSectra';
  src: url('../fonts/GT-Sectra-Book.eot');
  src: url('../fonts/GT-Sectra-Book.eot?#iefix') format('embedded-opentype'),
       url('../fonts/GT-Sectra-Book.woff2') format('woff2'),
       url('../fonts/GT-Sectra-Book.woff') format('woff');
  font-weight:  400;
  font-style:   normal;
  font-stretch: normal;
}

@font-face {
  font-family: 'GTSectra';
  src: url('../fonts/GT-Sectra-Book-Italic.eot');
  src: url('../fonts/GT-Sectra-Book-Italic.eot?#iefix') format('embedded-opentype'),
       url('../fonts/GT-Sectra-Book-Italic.woff2') format('woff2'),
       url('../fonts/GT-Sectra-Book-Italic.woff') format('woff');
  font-weight:  400;
  font-style:   italic;
  font-stretch: normal;
}

@font-face {
  font-family: 'GTSectra';
  src: url('../fonts/GT-Sectra-Medium.eot');
  src: url('../fonts/GT-Sectra-Medium.eot?#iefix') format('embedded-opentype'),
       url('../fonts/GT-Sectra-Medium.woff2') format('woff2'),
       url('../fonts/GT-Sectra-Medium.woff') format('woff');
  font-weight:  600;
  font-style:   normal;
  font-stretch: normal;
}
